<template>
  <div :class="isBottomDetal? 'common-header active':'common-header'">
    <el-row tag="div">
      <el-col>
        <div class="logo-container">
          <div class="logo" @click="backToHome">
            <img  src="../assets/imgs/logo.png" alt="windtalkLogo">
            <span class="logo-title">Windtalk</span>
          </div>
          <div class="navLink hidden-xs-only">
            <el-tabs v-model="navStatus" @tab-click="reviewType">
              <el-tab-pane label="Home" name="index">
              </el-tab-pane>
              <el-tab-pane label="Windtalk" name="windtalk">
              </el-tab-pane>
              <el-tab-pane label="DigitalAsset" name="digitalAsset">
              </el-tab-pane>
              <el-tab-pane label="WTO" name="wto">
              </el-tab-pane>
              <el-tab-pane label="Community" name="community">
              </el-tab-pane>
            </el-tabs>
          </div>
          <div class="phone-link hidden-sm-and-up">
            <div class="minus" @click="showNavList">
              <!-- <i class="el-icon-tickets navlist" @click="showNavList"></i> -->
              <span  :class="showPhoneLinkModel? 'active': ''"></span>
            </div>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-row v-if="showPhoneLinkModel" class="phone-link-list-model hidden-sm-and-up">
      <i @click="showNavList" class="el-icon-close close-position"></i>
      <ul class="phone-link-list">
        <li >
          <span :class="navStatus === 'index'? 'active' : ''" @click="navigateTo('index')">Home</span>
        </li>
        <li >
          <span :class="navStatus === 'windtalk'? 'active' : ''" @click="navigateTo('windtalk')">Windtalk</span>
        </li>
        <li >
          <span :class="navStatus === 'digitalAsset'? 'active' : ''" @click="navigateTo('digitalAsset')">Digital Asset</span>
        </li>
        <li >
          <span :class="navStatus === 'wto'? 'active' : ''" @click="navigateTo('wto')">WTO</span>
        </li>
        <li >
          <span :class="navStatus === 'community'? 'active' : ''"  @click="navigateTo('community')">Community</span>
        </li>
      </ul>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Vheader',
  data () {
    return {
      isBottomDetal: false,
      navStatus: 'index',
      showPhoneLinkModel: false
    }
  },
  computed: {
    path: function () {
      return this.$route.path === '/' ? 'index' : this.$route.path.slice(1)
    }
  },
  methods: {
    backToHome () {
      this.navStatus = 'index'
      this.$router.push('/index')
    },
    reviewType () {
      this.$router.push('/' + this.navStatus)
    },
    showNavList () {
      this.showPhoneLinkModel = !this.showPhoneLinkModel
    },
    navigateTo (val) {
      this.$router.push('/' + val)
      this.showNavList()
    },
    handleScroll () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      if (scrollTop === 0) {
        this.isBottomDetal = false
      } else {
        this.isBottomDetal = true
      }
    }
  },
  watch: {
    path: function () {
      this.navStatus = this.path
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus">
.common-header
  position fixed
  top 0
  left 0
  font-size 12px
  height 60px
  width 100%
  background #fff
  line-height 60px
  z-index 10
  &.active
    box-shadow 0px 2px 18px 0px rgba(197, 211, 242, 0.13)
  .minus
    display inline-block
    height 40px
    margin-top 10px
    line-height 40px
    span
      display inline-block
      height 2px
      border-radius 1px
      cursor pointer
      width 20px
      background #000
      position relative
      transition all 0.2s ease
      top -4px
      &.active
        transform rotate(90deg)
      &:after
        position absolute
        top 6px
        left 0
        content ''
        height 2px
        width 20px
        background #000
        border-radius 1px
      &:before
        position absolute
        top -6px
        left 0
        content ''
        height 2px
        width 20px
        background #000
        border-radius 1px
  .logo-container
    width 96%
    margin 0 auto
  .logo-title
    font-size 16px
  .logo
    line-height 60px
    float left
    cursor pointer
    user-select none
    span
      color #000
      text-decoration none
      font-weight 900
    img
      width 27px
      vertical-align top
      padding-top 16px
  .navLink
    float right
    padding-right 86px
    .el-tabs__item
      color #c3c5cc
      font-size 12px
      font-weight 600
    .el-tabs__item.is-active
      color #000735
    .el-tabs__header
      margin 0
    .el-tabs__active-bar
      bottom 12px
      color #a3baff
    .el-tabs__nav-wrap::after
      background #fff
  .phone-link
    float right
    .navlist
      font-size 20px
  .phone-link-list-model
    position fixed
    height 100%
    width 100%
    background #262626
    top 0
    left 0
    .close-position
      position absolute
      right 2%
      top 18px
      font-size 20px
      color #5f5f5f
  .phone-link-list
    padding-top 2.6rem
    li
      text-align center
      padding 0.4rem 0
      span
        color #4f4f4f
        cursor pointer
        line-height 1
        font-size 18px
        &.active
          color #949494
        &:hover
          color #949494
</style>



